// CheckBoxValue è¦æ±valueæ¯å¸å°ç±»åï¼ç»ä»¶è¦æ±valueæ¯trueæ¶ï¼å¼åºä¸ºtrueå­ç¬¦ä¸²
export class CheckBoxValue implements IValue{
    clearValue(): void {
        this.value = [];
    }
    // å¦æç»ä»¶çè¿åå¼æ¯å­ç¬¦ä¸²ï¼è½¬åä¸ºå¸å°å¼
    setOriginalValue(value: any[]): void {
        if(value.length){
            this.value = value.map(v => {
               if(typeof v == 'string'){
                 return true;
               }else {
                 return v;
               }
            });
         }else{
            this.value = value;
         }
    }
    // æCheckBoxValueçtrueå¸å°å¼ï¼è½¬åä¸ºç»ä»¶å¯è¯å«çå­ç¬¦ä¸²
    getOriginalValue() {
        if(this.value.length){
          return this.value.map(v => {
            if(v === true){
              return "true";
            }else{
              return v;
            }
          });
        }
        return this.value;
    }
    getControlType(): ControlType {
        return ControlType.BoolCheck;
    }
    value: any[];
    // CheckBoxValue åå§åï¼ä¼ å¥çå¼æªåä»»ä½ä¿®æ¹
    constructor(value: any[] = []){
        this.value = value;
    }
    isEmpty(): boolean {
        return this.value.length == 0;
    }
}